import { Component, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IManagedObject, InventoryService, IResultList, QueriesUtil } from '@c8y/client';
import { DataSourceModifier, ServerSideDataResult } from '@c8y/ngx-components';
import { DeviceGridColumn, DeviceGridService } from '@c8y/ngx-components/device-grid';
import { StatusOptionsService } from '../status-options';
import {
  LastUpdatedDateGridColumn,
  ServiceNameGridColumn,
  ServiceTypeGridColumn,
  StatusGridColumn
} from './columns';

const SERVICE_FRAGMENT = 'c8y_Service';

@Component({
  selector: 'c8y-services-device-tab',
  templateUrl: 'services-device-tab.component.html'
})
export class ServicesDeviceTabComponent {
  deviceId: string | number = this.route.snapshot.parent.data.contextData.id;

  sizeRequest: Promise<number>;
  sizeRequestDone = false;

  refresh$: EventEmitter<void> = new EventEmitter();
  pagination = {
    pageSize: 50,
    currentPage: 1
  };

  columns: DeviceGridColumn[] = [
    new StatusGridColumn(this.statusOptionsService),
    new ServiceNameGridColumn(),
    new ServiceTypeGridColumn(),
    new LastUpdatedDateGridColumn()
  ];

  serverSideDataCallback: any;

  private queriesUtil: QueriesUtil = new QueriesUtil();

  constructor(
    private route: ActivatedRoute,
    private inventoryService: InventoryService,
    private gridService: DeviceGridService,
    private statusOptionsService: StatusOptionsService
  ) {
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
    this.sizeRequest = this.inventoryService
      .childAdditionsList(this.deviceId, {
        query: `type eq ${SERVICE_FRAGMENT}`,
        withTotalPages: true,
        pageSize: 1
      })
      .then(response => {
        this.sizeRequestDone = true;
        return response?.paging?.totalPages;
      });
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    let serverSideDataResult: ServerSideDataResult;

    const query = this.queriesUtil.buildQuery(
      this.queriesUtil.addAndFilter(this.gridService.getQueryObj(dataSourceModifier.columns), {
        type: SERVICE_FRAGMENT
      })
    );

    const dataRequest: Promise<IResultList<IManagedObject>> =
      this.inventoryService.childAdditionsList(this.deviceId, {
        query,
        withTotalPages: true,
        pageSize: dataSourceModifier.pagination.pageSize,
        currentPage: dataSourceModifier.pagination.currentPage
      });

    const filtererdSizeRequest: Promise<number> = this.inventoryService
      .childAdditionsList(this.deviceId, {
        query,
        withTotalPages: true,
        pageSize: 1
      })
      .then(response => response?.paging?.totalPages);

    const [dataResponse, size, filteredSize] = await Promise.all([
      dataRequest,
      this.sizeRequest,
      filtererdSizeRequest
    ]);

    const { res, data, paging } = dataResponse;

    serverSideDataResult = {
      res,
      data,
      paging,
      filteredSize,
      size
    };

    return serverSideDataResult;
  }

  trackByName(_index, column: DeviceGridColumn): string {
    return column.name;
  }
}
