import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { InventoryService, ISource } from '@c8y/client';
import {
  Breadcrumb,
  BreadcrumbFactory,
  BreadcrumbItem,
  gettext,
  ViewContext
} from '@c8y/ngx-components';

@Injectable()
export class ServicesBreadcrumbFactory implements BreadcrumbFactory {
  private contextData;

  constructor(private inventoryService: InventoryService) {}

  async get(route: ActivatedRoute) {
    const { context, contextData } = route.snapshot.data;
    const { routeConfig } = route.snapshot;

    this.contextData = contextData || this.contextData;

    if (
      (context === ViewContext.Service || (routeConfig as any)?.context === ViewContext.Service) &&
      !!this.contextData
    ) {
      const serviceMO = (await this.inventoryService.detail(this.contextData, { withParents: true }))?.data;
      const breadcrumb: Breadcrumb = { items: [] };
      const breadcrumbItems: BreadcrumbItem[] = [];
      const device: ISource = serviceMO?.additionParents?.references[0]?.managedObject;

      if (device) {
        breadcrumbItems.push({
          label: device.name,
          path: `/device/${device.id}`
        });
        breadcrumbItems.push({
          label: gettext('Services'),
          path: `/device/${device.id}/services`
        });
        breadcrumbItems.push({
          label: serviceMO.name
        } as BreadcrumbItem);
      }

      breadcrumb.items = breadcrumbItems;
      return breadcrumb;
    }
  }
}
