import { ColumnConfig, getBasicInputArrayFormFieldConfig, gettext } from '@c8y/ngx-components';
import { BaseDeviceGridColumn } from '@c8y/ngx-components/device-grid';

export class ServiceTypeGridColumn extends BaseDeviceGridColumn {
  constructor(initialColumnConfig?: ColumnConfig) {
    super(initialColumnConfig);
    this.name = 'serviceType';
    this.path = 'serviceType';
    this.header = gettext('Type');

    this.filterable = true;

    this.filteringConfig = {
      fields: getBasicInputArrayFormFieldConfig({
        key: 'serviceTypes',
        label: gettext('Filter services by type'),
        addText: gettext('Add next`type`'),
        tooltip: gettext('Use * as a wildcard character'),
        placeholder: 'systemd'
      }),
      getFilter(model: any): any {
        const filter: any = {};
        if (model.serviceTypes.length) {
          filter.serviceType = { __in: model.serviceTypes };
        }
        return filter;
      }
    };

    this.sortable = true;
    this.sortingConfig = {
      pathSortingConfigs: [{ path: this.path }]
    };
  }
}
